.app__header {
  padding-top: 6rem;

  @media screen and (min-width: 901px) and (max-width: 1024px) {
    padding-top: 8rem;
  }

  @media screen and (min-width: 601px) and (max-width: 900px) {
  }

  @media screen and (max-width: 600px) {
    // padding-top: 3rem;
  }
}

.app__header-div {
  display: flex;

  @media screen and (max-width: 600px) {
    flex-direction: column;
  }
}

.app__header-info {
  flex: 1;

  margin: auto;

  text-align: center;

  .app__header-name {
    font-size: 4rem;
  }

  .app__header-profile {
    font-size: 1.85rem;
    color: #5e5e5e;
  }

  .app__header-icons {
    display: flex;
    justify-content: space-evenly;

    margin-top: 1rem;

    .app__header-web-icon {
      color: red;
    }

    .app__header-android-icon {
      color: #3ddc84;
    }
  }

  @media screen and (max-width: 600px) {
    background-color: var(--primary-color);
  }
}

.header__image-div {
  flex: 1;

  .header__image {
    width: 79.5%;

    @media screen and (max-width: 600px) {
      width: 100%;
    }
  }

  @media screen and (max-width: 600px) {
    padding: 3rem;
  }
}

.app__header-contact-div {
  margin-top: 6rem;
}

.app__header-button {
  padding: 1rem 2rem;
  font-size: 1.5rem;

  svg {
    font-size: 1.6rem;
  }
}
