.app {
  background-color: var(--primary-color);
  font-family: var(--font-base);
}

.app__whitebg {
  background-color: var(--white-color);
}

.app__primarybg {
  background-color: var(--primary-color);
}

.app__container {
  width: 100%;
  min-height: 100vh;

  display: flex;
  flex-direction: row;
}

.app__flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__wrapper {
  flex: 1;
  width: 100%;
  flex-direction: column;
  padding: 4rem 2rem;

  @media screen and (max-width: 450px) {
    padding: 4rem 1rem 2rem;
  }
}

a {
  text-decoration: none;
}

a:visited {
  color: black;
}

.head-text {
  font-size: 2.75rem;
  font-weight: 800;
  text-align: center;
  color: var(--black-color);
  text-transform: capitalize;

  span {
    color: var(--secondary-color);
  }

  @media screen and (min-width: 2000px) {
    font-size: 4rem;
  }

  @media screen and (max-width: 600px) {
    font-size: 2rem;
  }
}

.p-text {
  font-size: 0.8rem;
  text-align: left;
  color: var(--gray-color);
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.app__social-media {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: fit-content;
  margin-left: 4rem;

  position: fixed;
  z-index: 2;

  div {
    padding: 0.75rem;

    svg {
      cursor: pointer;
    }

    transition: all 0.3s ease-in-out;

    &:hover {
      svg {
        color: var(--secondary-color);
      }
    }

    @media screen and (min-width: 2000px) {
      svg {
        width: 30px;
        height: 30px;
      }
    }
  }

  @media screen and (max-width: 600px) {
    position: absolute;
    margin-left: 0.5rem;
    bottom: 5%;

    div {
      padding: 0.5rem;
    }
  }
}

.app__navigation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  right: 0;
  width: fit-content;
  height: 100%;
  margin-right: 2rem;

  position: fixed;
  z-index: 2;

  .app__navigation-dot {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #e0e0e0;
    margin: 0.5rem;

    transition: background-color 0.2s ease-in-out;

    &:hover {
      background-color: var(--secondary-color);
    }

    @media screen and (min-width: 2000px) {
      width: 20px;
      height: 20px;
    }
  }

  .app__navigation-dot-active {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: var(--secondary-color);
    margin: 0.5rem;

    transition: background-color 0.2s ease-in-out;

    &:hover {
      background-color: var(--secondary-color);
    }

    @media screen and (min-width: 2000px) {
      width: 20px;
      height: 20px;
    }
  }

  @media screen and (max-width: 600px) {
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .app__navigation,
  .app__social {
    display: none;
  }

  .copyright {
    padding: 2rem;
  }
}

.app__heading3 {
  text-align: center;
  margin: 1rem 0;
  font-size: x-large;
}

.app__heading {
  margin-top: 2rem;
}

.app__section {
  min-height: 100vh;
  padding: 5rem 8rem 0 8rem;

  @media screen and (max-width: 600px) {
    padding: 0;
  }
}

.app__button {
  background-color: var(--secondary-color);
  color: white;
  border-radius: 15px;

  border: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;

  svg {
    vertical-align: text-top;
  }

  &:hover {
    background-color: #1db360;
  }
}
