.app__about-div {
  display: flex;
  flex-direction: row;
}

.app__about-description {
  width: 50%;
  padding: 4rem 3rem;
  p {
    font-size: xx-large;
    text-align: justify;

    span {
      font-weight: 600;
    }
  }

  button {
    margin-top: 2rem;
    padding: 1rem 2rem;
    font-size: 1.5rem;

    svg {
      font-size: 1.6rem;
    }
  }
}

.app__profiles {
  display: flex;
  justify-content: space-evenly;
  padding: 4rem;

  width: 50%;

  @media screen and (max-width: 600px) {
    flex-direction: column;
    padding: 0;
  }
}

.app__profile-item {
  display: flex;
  flex-direction: column;

  width: 23%;

  img {
    width: 100%;
    border-radius: 15px;
    object-fit: cover;

    @media screen and (max-width: 600px) {
      border-radius: 10px;
    }
  }

  @media screen and (max-width: 600px) {
    width: 100%;
    padding: 1rem 5rem;
  }
}
